require('./logic-sequencer.css');

window.sequencer = (function () {

    // Hack to prevent iframe from screen capture
    var item = sessionStorage.getItem('capturingScreen');
    if (item || !parent.jQuery) return;

    if (typeof (jQuery) == "undefined") {
        var iframeBody = document.getElementsByTagName("body")[0];
        var jQuery = function (selector) {
            return parent.jQuery(selector, iframeBody);
        };
        var $ = jQuery;
    }

    var sequencer = $('#sequencer');
    var $body = sequencer.find('.fixedTable-body div');
    var $sidebar = sequencer.find('.fixedTable-sidebar table');
    var $header = sequencer.find('.fixedTable-header .left table');
    var $module = sequencer.find(".content-left table:nth-child(2)");
    $($body).scroll(function () {
        $($sidebar).css('margin-top', -$($body).scrollTop());
        var left = $($body).scrollLeft();
        $($header).css('margin-left', left == 0 ? -2 : -left);
        return $($module).css('margin-top', left == 0 ? -2 : -left);
    });
        
    var $body1 = sequencer.find('.fixedTable-sidebar table tbody');
    for(var i=0; i<40; i++){
        var tr = document.createElement('tr');
        for(var j=0; j<6; j++){
            var td = document.createElement('td');
            if(j===5)
                td.innerHTML = i + 1;
            tr.appendChild(td);
        }
        $body1.append(tr);
    }

    var $body2 = sequencer.find('.fixedTable-body table tbody');
    for(var i=0; i<40; i++){
        var tr = document.createElement('tr');
        for(var j=0; j<40; j++){
            var td = document.createElement('td');;
            tr.appendChild(td);
        }
        $body2.append(tr);
    }

    var $body3 = sequencer.find('.rotatedTable table tbody');
    for(var i=0; i<40; i++){
        var tr = document.createElement('tr');
        var td = document.createElement('td');;
        tr.appendChild(td);
        $body3.append(tr);
    }

    // Events
    // Module cell clicked
    sequencer.find('.rotatedTable table tbody tr').on('click', function(evt){
        var target = null;
        if(evt.target.nodeName === 'TR')
            target = evt.target.children[0];
        else
            target = evt.target;
        var rows = $('.rotatedTable table tbody td');
        for(var i=0; i<rows.length; i++) {
            rows[i].style.backgroundColor = '';
        }
        API.selectedColumn = null;
        if ( API.selectedColumn !== target && target.innerHTML) {
            target.style.backgroundColor = '#D903FF';
            API.selectedColumn = target;
        }
    });

    sequencer.find('.fixedTable-sidebar table tbody tr').on('click', function(evt){
        var target = null;
        if(evt.target.nodeName === 'TD')
            target = evt.target.parentElement;
        else
            target = evt.target;
        if(API.selectedRow)
            API.selectedRow.style.backgroundColor = '';
        if(API.selectedRow && API.selectedRow === target) {
            API.selectedRow = null;
        } else if (target.children[0].innerHTML) {
            target.style.backgroundColor = 'aquamarine';
            API.selectedRow = target; 
            var index = findIndex(target);
            API.selectedRow.data = API.sequence.rows[index];
        }
    });

    sequencer.find('.fixedTable-body table tbody tr').on('dblclick', function(evt){
        var row = findIndex(evt.target.parentElement);
        var column = findIndex(evt.target);
        var moduleRow = sequencer.find('.rotatedTable table tbody td')[column];
        var module = moduleRow.innerHTML;
        if(!module) return;
        var mod = API.sequence.columns[column]
        var cmd = mod.commands[row];
        API.parent.selectAction(module, mod.id, mod.type, row, column, cmd); // was: var action = API.parent.selectAction(module, mod.id, mod.type, row, column, cmd);
    });

    function clear(){
        var modules = $('#moduleTable tr');
        var commandHeader = $('#commandHeader th');
        for(var i=0; i<modules.length; i++) {
            modules[i].children[0].innerHTML = '';
            modules[i].style.backgroundColor = '';
            $(commandHeader[i]).css({'min-width':20,'width':20});
            $(modules[i].children[0]).height(32);
        }
        var steps = $('#stepTable tr');
        for(var i=0; i<steps.length; i++) {
            steps[i].children[0].innerHTML = '';
            steps[i].children[1].innerHTML = '';
            steps[i].children[2].innerHTML = '';
            steps[i].children[3].innerHTML = '';
            steps[i].children[4].innerHTML = '';
            steps[i].style.backgroundColor = '';
        }
        var commands = $('#commandTable tr');
        for(var i=0; i<commands.length; i++) {
            for(var j=0; j<commands[i].children.length; j++) {
                commands[i].children[j].innerHTML = '';
                $(commands[i].children[j]).width(20);
            }
        }
        API.selectedColumn = null;
        API.selectedCommand = null;
        API.selectedRow = null;
    }

    function load(){
        for(var i=0; i<API.sequence.columns.length; i++) {
            API.addModule(API.sequence.columns[i]);
            for(var j=0; j<API.sequence.columns[i].commands.length; j++)
                API.addCommand(API.sequence.columns[i].commands[j], true);
        }
        for(var i=0; i<API.sequence.rows.length; i++)
            API.addStep(API.sequence.rows[i]);
    }

    function addModule(module) {
        if(module.index === undefined){
            module.index = API.sequence.columns.length;
            API.sequence.columns[module.index] = module;
        }
        var row = $('.rotatedTable table tbody td')[module.index];
        var td = document.createElement('td');
        td.innerHTML = module.name;
        $(row).replaceWith(td);
    }

    function removeModule() {
        var index = findIndex(API.selectedColumn.parentElement);
        var tr = document.createElement('tr');
        var td = document.createElement('td');
        tr.appendChild(td);
        var row = $(API.selectedColumn.parentElement);
        var body = API.selectedColumn.parentElement.parentElement;
        $(row).remove();
        body.appendChild(tr);
        API.sequence.columns.splice(index, 1);
        API.selectedColumn = null;
        removeCommandHeader($('#commandHeader thead tr'), index);
        removeCommandColumns($('#commandTable tbody'), index);
        addCommandColumns($('#commandTable tbody'));
        updateModuleIndexs(index);
    }

    function updateModuleIndexs(index){
        for(var i=0; i<API.sequence.columns.length; i++){
            if(API.sequence.columns[i].index > index){
                API.sequence.columns[i].index -= 1;
                for(var j=0; j<API.sequence.columns[i].commands.length; j++){
                    if(!API.sequence.columns[i].commands[j]) continue;
                    API.sequence.columns[i].commands[j].column -= 1;
                }
            } 
        }
    }

    function removeCommandHeader($tr, index) {
        var th = $tr.children()[index];
        $(th).remove();
        var kids = $tr.children();
        for(var i=0; i<kids.length; i++) {
            if(i < index) continue;
            kids[i].innerHTML = i+1;
        }
        th = document.createElement('th');
        th.innerHTML = '42';
        $tr.append(th);
    }

    function removeCommandColumns($body, column) {
        var kids = $body.children();
        for(var i=0; i<kids.length; i++){
            var td = kids[i].children[column];
            $(td).remove();
        }
    }

    function addCommandColumns($body) {
        var kids = $body.children();
        for(var i=0; i<kids.length; i++){
            var td = document.createElement('td');
            kids[i].appendChild(td);
        }
    }

    function addCommand(command, isLoading) {
        if(!command) return;
        var tr = $body2.children()[command.row];
        var td = tr.children[command.column];
        td.innerHTML = command.name;
        var width = $(td).width();
        var outerWidth = $(td).outerWidth();
        $($('#commandHeader th')[command.column]).css({'min-width':width,'width':width});
        $($('#moduleTable td')[command.column]).height(outerWidth);
        if(!isLoading)
            API.sequence.columns[command.column].commands[command.row] = command; 
    }

    function removeCommand(row, column) {
        API.sequence.columns[column].commands[row] = null;
        var tr = $body2.children()[row];
        var td = tr.children[column];
        td.innerHTML = '';
        var width = $(td).width();
        var outerWidth = $(td).outerWidth();
        $($('#commandHeader th')[column]).css({'min-width':width,'width':width});
        $($('#moduleTable td')[column]).height(outerWidth);
    }

    function addStep(row) {
        if(row.index === undefined) {
            row.index = API.sequence.rows.length;
            API.sequence.rows[row.index] = row;
        }
        var tableRow = $('#stepTable tbody').children()[row.index];
        tableRow.children[0].innerHTML = 'S' + (row.index + 1);
        tableRow.children[1].innerHTML = row.step.description;
        tableRow.children[2].innerHTML = 'T' + (row.index + 1);
        tableRow.children[3].innerHTML = row.transition.description;
        tableRow.children[4].innerHTML = row.transition.interval;
    }

    function removeStep() {
        if(!API.selectedRow) return;
        var steps = API.selectedRow.parentElement.children;
        var index = findIndex(API.selectedRow);
        var tr = document.createElement('tr');
        tr.appendChild(document.createElement('td'));
        tr.appendChild(document.createElement('td'));
        tr.appendChild(document.createElement('td'));
        tr.appendChild(document.createElement('td'));
        tr.appendChild(document.createElement('td'));
        tr.appendChild(document.createElement('td'));
        API.selectedRow.parentElement.appendChild(tr);
        $(API.selectedRow).remove();
        var commandRow = $('#commandTable tr')[index];
        tr = document.createElement('tr');
        for(var i=0; i<40; i++)
            tr.appendChild(document.createElement('td'));
        $('#commandTable tbody').append(tr);
        $(commandRow).remove();
        for(var i=0; i<steps.length; i++) {
            steps[i].children[5].innerHTML = i + 1;
        }
        API.sequence.rows.splice(index, 1);
        removeCommandRow(index);
        resizeCommandColumns();
        renumberSteps(index);
    }

    function findIndex(child) {
        var parent = child.parentElement;
        for(var i=0; i<parent.children.length; i++){
            if(parent.children[i] === child)
                return i;
        }
        return -1;
    }

    function removeCommandRow(index){
        for(var i=0; i<API.sequence.columns.length; i++) {
            API.sequence.columns[i].commands.splice(index, 1);
            for(var j=0; j<API.sequence.columns[i].commands.length; j++){
                if(API.sequence.columns[i].commands[j] && API.sequence.columns[i].commands[j].row > index)
                    API.sequence.columns[i].commands[j].row -= 1;
            }
        }
    }

    function resizeCommandColumns() {
        var columns = $('#commandTable tr')[0].children;
        for(var i=0; i<columns.length; i++) {
            var width = $(columns[i]).width();
            var outerWidth = $(columns[i]).outerWidth();
            $($('#commandHeader th')[i]).css({'min-width':width,'width':width});
            $($('#moduleTable td')[i]).height(outerWidth);
        }
    }

    function renumberSteps(start) {
        var steps = $('#stepTable tr');
        for(var i=0; i<API.sequence.rows.length; i++){
            if(i < start) continue;
            API.sequence.rows[i].index -= 1;
            steps[i].children[0].innerHTML = 'S' + (API.sequence.rows[i].index + 1);
            steps[i].children[2].innerHTML = 'T' + (API.sequence.rows[i].index + 1);
        }
    }

    var API = {
        processCell: null,
        sequence: null,
        selectedColumn: null,
        selectedCommand: null,
        selectedRow: null,
        clear: clear,
        load: load,
        addModule: addModule,
        removeModule: removeModule,
        addCommand: addCommand,
        removeCommand: removeCommand,
        addStep: addStep,
        removeStep: removeStep
    }
    return API;

})();


